.nav {
  &__list {
    display: flex;
  }
  &__link {
    display: inline-block;
    padding: 29px 12px;
    font-style : normal;
    font-weight: normal;
    font-size  : 16px;
    line-height: 24px;
    color: #191D32;
    white-space: nowrap;
    transition: 0.2s;
    @media(max-width:$md) {
      padding: 16px 12px;
    }
    &:hover {
      color: $pur-c;
      background: $pur-c-light;
      border-radius: 4px;
    }
  }
}