.hero {
  &__container {
    width    : 100%;
    max-width: 1440px;
    margin   : 0 auto;
    @media(max-width:$sm) {
      padding: 0 15px;
    }
    @media(max-width:$xs) {
      padding: 0 10px;
    }
  }
}

.banner {
  padding-top        : 150px;
  padding-left       : 160px;
  padding-bottom     : 139px;
  font-style         : normal;
  font-weight        : bold;
  color              : $c-white;
  background-repeat  : no-repeat;
  background-position: top left;
  background-size    : cover !important;
  @media(max-width:$md) {
    padding: 95px;
  }
  @media(max-width:$sm) {
    padding: 60px;
    padding-left: 40px;
    border-radius: 20px;
    overflow     : hidden;
  }
  @media(max-width:450px) {
    padding: 40px;
    padding-left: 25px;
  }
  @media(max-width:321px) {
    padding     : 34px;
    padding-left: 18px;
  }
  &__title {
    max-width    : 580px;
    margin-bottom: 25px;
    font-size    : 88px;
    line-height  : 80px;
    text-shadow  : $text-shadow;
    @media(max-width:$md) {
      max-width: 430px;
      font-size : 60px;
      line-height : 68px;
    }
    @media(max-width:$xs) {
      max-width : 280px;
      font-size : 40px;
      line-height : 48px;
      margin-bottom: 15px;
    }
    @media(max-width:450px) {
      max-width    : 215px;
      font-size    : 32px;
      line-height  : 36px;
      margin-bottom: 10px;
    }
    @media(max-width:321px) {
      max-width    : 170px;
      font-size    : 24px;
      line-height  : 26px;
      margin-bottom: 5px;
    }
  }

  &__promo-date {
    font-size  : 32px;
    line-height: 36px;
    text-shadow: $text-shadow;
    @media(max-width:$md) {
      font-size  : 24px;
      line-height: 28px;
    }
     @media(max-width:$xs) {
      font-size : 20px;
      line-height: 22px;
    }
    @media(max-width:450px) {
      font-size    : 14px;
      line-height  : 16px;
    }
    @media(max-width:321px) {
      font-size: 12px;
      line-height: 12px;
    }
  }
}
.block-title {
  font-style : normal;
  font-weight: bold;
  font-size  : 32px;
  line-height: 36px;
  text-align : center;
  color      : $c-title;
  @media(max-width: 400px) {
    font-size: 20px;
    line-height: 28px;
  }
}
.cards {
  &__container {
    padding-top: 20px;
    @media(max-width: $sm) {
      padding-top: 20px;
    }
    @media(max-width: 680px) {
      padding-left: 0;
    }
  }
  &__title {
    margin-bottom: 32px;
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -10px;
    @media(max-width: 1420px) {
      justify-content: center;
    }
    @media(max-width: 680px) {
      margin: 0;
    }
  }
}

.card {
  position: relative;
  display       : flex;
  flex-direction: column;
  flex: 0 1 100%;
  max-width: 302px;
  margin: 0 10px;
  margin-bottom: 60px;
  padding       : 20px;
  border-radius : $card-border-radius;
  box-shadow    : $card-shadow;
  transition: 0.2s;
  cursor: pointer;
  @media(max-width:$sm) {
    margin-bottom: 20px;
  }
  @media(max-width: 680px) {
    flex-direction: row;
    max-width: none;
    margin-left: 0;
    margin-right: 0;
    padding: 0;
    box-shadow : none;
  }
  &:hover {
    box-shadow : $card-shadow-hover;
    @media(max-width: 680px) {
      box-shadow    : none;
    }
  }
  &__label {
    position: absolute;
    top: 30px;
    left: 10px;
    width: 56px;
    height: 32px;
    padding: 4px;
    text-align: center;
    font-style : normal;
    font-weight: bold;
    font-size  : 14px;
    line-height: 24px;
    letter-spacing: 0.02em;
    color           : $c-white;
    background-color: $pur-c;
    border-radius   : $border-r;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    z-index: 2;
    @media(max-width: 680px) {
      display: none;
    }
  }
  &__img {
    position: relative;
    max-width: 100%;
    height: 224px;
    margin-bottom: 12px;
    overflow: hidden;
    border-radius: $border-r;
    
    @media(max-width: 680px) {
      height: auto;
      margin-right: 16px;
      margin-bottom: 0;
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
    @media(max-width: 400px) {
      flex: 0 1 200px;
    }
    img {
      display: block;
      max-width: 100%;
      height   : auto;
      @media(max-width: 400px) {
        max-width: none;
      }
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    flex: 0 1 100%;
  }
  &__title {
    margin-bottom: 8px;
    font-style : normal;
    font-weight: bold;
    font-size  : 16px;
    line-height: 24px;
    text-align: center;
    color: $c-title;
    @media(max-width:500px) {
      text-align: left;
    }
  }
  &__descr {
    margin-bottom: 24px;
    font-style : normal;
    font-weight: normal;
    font-size  : 16px;
    line-height: 24px;
    text-align: center;
    color: #707584;
    @media(max-width:500px) {
      margin-bottom: 16px;
      text-align: left;
      font-size: 12px;
      line-height: 14px;
    }
  }
  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: auto;
  }
  &__button {
    margin-right: 27px;
    padding: 7px 16px;
    font-style : normal;
    font-weight: bold;
    font-size  : 14px;
    line-height: 24px;
    letter-spacing: 0.02em;
    color: $c-white;
    background-color: $pur-c;
    border-radius: $border-r;
    transition: 0.2s;
    &:hover {
      color: $pur-c;
      background-color: $pur-c-light;
    }
    @media(max-width:500px) {
      display: none;
    }
  }
  &__button-price {
    display: none;
    min-width: 100px;
    padding: 4px 16px;
    @media(max-width:500px) {
      display: block;
    }
  }
  &__price {
    width: 100%;
    text-align: center;
    padding: 0 10px;
    font-style : normal;
    font-weight: bold;
    font-size  : 20px;
    line-height: 28px;
    color: $c-title;
    @media(max-width: 680px) {
      text-align: right;
      padding-right: 25px;
    }
    @media(max-width:500px) {
      display: none;
    }
  }
}