.page-header {
  display          : flex;
  align-items      : center;
  padding-left: 20px;
  min-height       : 82px;
  @media(max-width:$md) {
    flex-wrap: wrap;
    padding-left: 0;
  }
  @media(max-width:$xs) {
    padding-left: 7px;
    padding-top: 11px;
    padding-bottom: 13px;
    min-height: auto;
  }
  &__logo {
    order: 1;
  }
  &__phone {
    margin-left: 34px;
    order: 2;
    @media(max-width:$xs) {
      display: none;
    }
  }
  &__nav {
    margin-left: auto;
    order: 3;
    @media(max-width:$md) {
      width: 65%;
      margin-left: 0;
      margin-right: auto;
      order: 4;
    }
    @media(max-width:$xs) {
      display: none;
    }
  }
  &__cart {
    margin-left: 63px;
    margin-right: 32px;
    order: 4;
    @media(max-width:$md) {
      margin-left: 0;
      margin-left: auto;
      margin-right: 25px;
      order: 5;
    }
    @media(max-width:$xs) {
      display: none;
    }
  }
  &__login {
    order: 5;
    @media(max-width:$md) {
      margin-left: auto;
      order: 3;
    }
    @media(max-width:$xs) {
      display: none;
    }
  }
}

.phone {
  display    : block;
  font-style : normal;
  font-weight: normal;
  font-size  : 16px;
  line-height: 24px;
  color      : #191D32;
  //transition: 0.1s;
  &:hover {
    color: #000000;
    transform: scale(1.01);
  }
}

.promo-text {
  display    : block;
  font-style : normal;
  font-weight: normal;
  font-size  : 12px;
  line-height: 16px;
  color: #707584;
  white-space: nowrap;
}
.cart {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 38px;
  height: 38px;
  padding: 10px;
  border    : 1px solid $pur-c;
  border-radius: $border-r;
  transition: 0.2s;
  &:hover {
    background-color: $pur-c-light;
    border-color: $pur-c-light;
  }
  &__label {
    position: absolute;
    left: 28px;
    bottom: 28px;
    display: inline-block;
    min-width: 33px;
    padding: 0 4px;
    text-align: center;
    font-style : normal;
    font-weight: bold;
    font-size  : 14px;
    line-height: 24px;
    letter-spacing: 0.02em;
    color: #FFFFFF;
    border-radius: $border-r;
    background-color: $pur-c;
    z-index: 1;
  }
}
.login {
  display: flex;
  align-items: center;
  padding: 7px 11px;
  padding-right: 18px;
  border : 1px solid $pur-c;
  border-radius: $border-r;
  transition: 0.2s;
  &:hover {
    background-color: $pur-c-light;
    border-color    : $pur-c-light;
  }
  span {
    display: block;
    margin-left: 10px;
    font-style : normal;
    font-weight: bold;
    font-size  : 14px;
    line-height: 24px;
    letter-spacing: 0.02em;
    color: $pur-c;
  }
}
.options {
  position: absolute;
  top: 20px;
  left: 20px;
  padding: 12px;
  width: 48px;
  height: 48px;
  border-radius: $border-r;
  background-color: transparent;
  transition: 0.2s;
  @media(max-width:$sm) {
    display: none;
  }
  &:hover {
    transform: scale(1.05);
  }
}