* {
  box-sizing: border-box;
}

body {
  min-width  : 320px;
  font-family: $ff;
  font-size  : 16px;
  line-height: 1.2;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul,
p {
  margin : 0;
  padding: 0;
}

a {
  display        : inline-block;
  text-decoration: none;
  color          : #000000;
  outline        : none;
}

ul {
  list-style-type: none;
}

.container {
  position     : relative;
  width        : 100%;
  max-width    : 1440px;
  margin       : 0 auto;
  padding      : 0 66px;
  padding-right: 86px;

  @media(max-width:$md) {
    padding-right: 16px;
  }

  @media(max-width: $sm) {
    padding: 0 16px;
  }
  @media(max-width: $xs) {
    padding-right: 9px;
  }
}

button {
  background-color: transparent;
  border          : none;
  outline         : none;
  cursor          : pointer;
}

.btn {
  position        : relative;
  display         : inline-block;
  padding         : 6px;
  font-weight     : 600;
  font-size       : 13px;
  line-height     : 18px;
  text-align      : center;
  border          : none;
  border-radius   : $border-r;
  background-color: transparent;
  text-decoration : none;
  outline         : none;
  cursor          : pointer;
  white-space     : nowrap;
}

.overlay {
  display         : none;
  position        : absolute;
  top             : 0;
  left            : 0;
  width           : 100%;
  height          : 100vh;
  background-color: rgba($color: #000000, $alpha: 0.16);
  z-index         : 10;

  &--active {
    display: block;
  }
}

.lock {
  overflow: hidden;
}

.show {
  display: flex !important;
}