.burger {
  position        : relative;
  display         : none;
  padding         : 0;
  margin-left: auto;
  font-size       : 0;
  text-decoration : none;
  border          : none;
  outline         : none;
  cursor          : pointer;
  background-color: transparent;
  order: 10;

  @media (max-width: $xs) {
    display        : flex;
    justify-content: center;
    align-items    : center;
    width : 20px;
    height : 14px;
    z-index        : 14;
  }


  &::before,
  &::after {
    content         : '';
    position        : absolute;
    width : 20px;
    height : 2px;
    background-color: #000000;
    transition      : 0.3s linear;
  }

  &::before {
    top : 0;
    left: 0;
  }

  &::after {
    bottom: 0;
    left  : 0;
  }

  &__active {
    &::before {
      transform: rotate(45deg);
      top : 8px;
    }

    &::after {
      transform: rotate(-45deg);
      top : 8px;
    }

    .burger__line {
      transform: scale(0);
    }
  }

  &__line {
    position        : relative;
    display         : block;
    width : 20px;
    height : 2px;
    background-color: #000000;
    transition      : 0.2s linear;
  }

}